body {
  background-color: whitesmoke;
}

.raq-header {
  margin-top: 120px;
}

h1{
  font-size: 55px;
  color: indianred;
}

.quoteStyle {
  color: rgb(13, 13, 78);
  background-color: white;
  min-height: 300px;
  border-radius: 20px;
}

.quoteStyleText {
  font-size: 30px;
  font-weight: 500;
  font-style: italic;
  width: 75%;
  min-height: 150px;
  margin: 0 auto;
  padding-top: 10%
}

.bottom-character {
  text-align: right;
}

.rightCornerLinqs {
  position: fixed;
  bottom: 0px;
  right: 5px;
  color: gray;
  font-style: italic;
}

.raq-button {
  min-width: 50%;
  min-height: 75px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

